html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  display: inline-block;
  text-decoration: none;
  color: #546e7a;
}

ul,
li {
  margin: 0;
  padding: 0;
  letter-spacing: normal;
}

body {
  font: 400 15px/24px 'Roboto-Regular', sans-serif;
  color: #546e7a;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

@font-face {
  font-family: 'Roboto-Regular',sans-serif;
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

.header__top-inner {
  display: flex;
  justify-content: space-between;
  height: 90px;
  align-items: center;
}

.header__text {
  margin-left: auto;
  margin-right: 50px;
}

.header__text span {
  color: #e74c3c;
}

.header__btn {
  padding: 4px;
  width: 72px;
  border-radius: 4px;
  text-align: center;
  color: #ffffff;
  font-size: 12px;
}

.header__btn-login {
  background: #8bc34a;
}

.header__btn-register {
  background: #e74c3c;
  margin-left: 7px;
}

.header__menu {
  background: #263238;
}

.menu {
  display: flex;
  align-items: center;
  height: 60px;
}

.menu__list li {
  display: inline-block;
}

.menu__list li + li {
  margin-left: 30px;
}

.menu__list a {
  font-size: 14px;
  color: #fff;
}

.search {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  min-height: 533px;
}

.search__inner {
  padding: 135px 0 100px;
  color: #ffffff;
  text-align: center;
}

.search__inner-title {
  margin-bottom: 28px;
  font-size: 48px;
  font-weight: 700;
  line-height: 72px;
}

.search__inner-text {
  margin-bottom: 50px;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
}

.search__inner-form {
  max-width: 670px;
  margin: 0 auto;
  position: relative;
}

.search__inner-form input {
  display: block;
  width: 100%;
  box-shadow: 0px 1px 10px 0px rgba(124, 178, 64, 0.75);
  border-radius: 4px;
  height: 56px;
  border: none;
  font-size: 14px;
  font-weight: 500;
  padding-left: 25px;
  padding-right: 60px;
}

.search__inner-form input::placeholder {
  color: #90a4ae;
  font-weight: 500;
}

.search__inner-form button {
  background: transparent;
  position: absolute;
  border: none;
  right: 20px;
  top: 16px;
}